// Variables we use here
//   $font-family-base
//   $font-size-base
//   $font-weight-base
//   $line-height-base
//   $color-base
//   $body-bg
//   $headings-margin-bottom
//   $paragraph-margin-bottom

// Reboot
//
// Normalization of HTML elements, manually forked from Normalize.css to remove
// styles targeting irrelevant browsers while applying new styles.
//
// Normalize is licensed MIT. https://github.com/necolas/normalize.css


// Document
//
// 1. Change from `box-sizing: content-box` so that `width` is not affected by `padding` or `border`.
// 2. Change the default font family in all browsers.
// 3. Correct the line height in all browsers. (normalize.css)
// 4. Prevent adjustments of font size after orientation changes in IE on Windows Phone and in iOS. (normalize.css)
// 5. Setting @viewport causes scrollbars to overlap content in IE11 and Edge, so
//    we force a non-overlapping, non-auto-hiding scrollbar to counteract.
// 6. Change the default tap highlight to be completely transparent in iOS.

*,
*::before,
*::after {
  box-sizing: border-box; // 1
}

html {
  font-family: sans-serif; // 2
  line-height: 1.15; // 3 (normalize.css)
  -webkit-text-size-adjust: 100%; // 4 (normalize.css)
  -ms-text-size-adjust: 100%; // 4
  -ms-overflow-style: scrollbar; // 5
  // -webkit-tap-highlight-color: rgba($black, 0); // 6
  -webkit-tap-highlight-color: black; // 6
}

// IE10+ doesn't honor `<meta name="viewport">` in some cases.
@-ms-viewport {
  width: device-width;
}

// Shim for "new" HTML5 structural elements to display correctly (IE10, older browsers)
// normalize.css: Render the `main` element consistently in IE.
article,
aside,
figcaption,
figure,
footer,
header,
hgroup,
main, // normalize.css
nav,
section {
  display: block; // normalize.css
}

// Body
//
// 1. Remove the margin in all browsers. (normalize.css)
// 2. As a best practice, apply a default `background-color`.
// 3. Set an explicit initial text-align value so that we can later use the
//    the `inherit` value on things like `<th>` elements.

body {
  margin: 0; // 1 (normalize.css)
  font-family: $font-family-base;
  font-size: $font-size-base;
  font-weight: $font-weight-base;
  line-height: $line-height-base;
  // color: $body-color;
  color: $color-base;
  text-align: left; // 3
  background-color: $body-bg; // 2
}

// Suppress the focus outline on elements that cannot be accessed via keyboard.
// This prevents an unwanted focus outline from appearing around elements that
// might still respond to pointer events.
//
// Credit: https://github.com/suitcss/base
[tabindex="-1"]:focus {
  outline: 0 !important; // TODO
}


// Content grouping
//
// 1. Add the correct box sizing in Firefox. (normalize.css)
// 2. Show the overflow in Edge and IE. (normalize.css)

hr {
  box-sizing: content-box; // 1 (normalize.css)
  height: 0; // 1 (normalize.css)
  overflow: visible; // 2 (normalize.css)
}


//
// Typography
//

//
// normalize.css:
//
// /**
// * Correct the font size and margin on `h1` elements within `section` and
// * `article` contexts in Chrome, Firefox, and Safari.
// */
//
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

// Remove top margins from headings
//
// By default, `<h1>`-`<h6>` all receive top and bottom margins. We nuke the top
// margin for easier control within type scales as it avoids margin collapsing.
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: $headings-margin-bottom;
}

// Reset margins on paragraphs
//
// Similarly, the top margin on `<p>`s get reset. However, we also reset the
// bottom margin to use `rem` units instead of `em`.
p {
  margin-top: 0;
  margin-bottom: $paragraph-margin-bottom;
}

// Abbreviations
//
// 1. Remove the bottom border in Firefox 39-.
// normalize.css: 1. Remove the bottom border in Chrome 57-
// 2. Add the correct text decoration in Chrome, Edge, IE, Opera, and Safari. (normalize.css)
// 3. Add explicit cursor to indicate changed behavior.
// 4. Duplicate behavior to the data-* attribute for our tooltip plugin

abbr[title], // normalize.css
// abbr[data-original-title] // 4 { // Removed
{
  text-decoration: underline; // 2 (normalize.css)
  text-decoration: underline dotted; // 2 (normalize.css)
  cursor: help; // 3
  border-bottom: 0; // 1
  // normalize.css: border-bottom: none; // 1
}

address {
  // margin-bottom: 1rem;
  margin-bottom: 1em;
  font-style: normal;
  line-height: inherit;
}

ol,
ul,
dl {
  margin-top: 0;
  // margin-bottom: 1rem;
  margin-bottom: 1em;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

dt {
  // font-weight: $dt-font-weight;
  font-weight: 700;
}

dd {
  // margin-bottom: .5rem;
  margin-bottom: .5em;
  margin-left: 0; // Undo browser default
}

blockquote {
  // margin: 0 0 1rem;
  margin: 0 0 1em;
}

dfn {
  font-style: italic; // Add the correct font style in Android 4.3-
}

b,
strong {
  font-weight: bolder; // Add the correct font weight in Chrome, Edge, and Safari (normalize.css)
}

small {
  font-size: 80%; // Add the correct font size in all browsers (normalize.css)
}

//
// Prevent `sub` and `sup` elements from affecting the line height in
// all browsers. (normalize.css)
//

sub,
sup {
  position: relative; // normalize.css
  font-size: 75%; // normalize.css
  line-height: 0; // normalize.css
  vertical-align: baseline; // normalize.css
}

sub {
  bottom: -.25em; // normalize.css
}

sup {
  top: -.5em; // normalize.css
}

//
// Moved to links.scss
//
// Links
//

// 1. Remove the gray background on active links in IE 10. (normalize.css)

// a {
//   color: $link-color;
//   text-decoration: $link-decoration;
//   background-color: transparent; // 1 (normalize.css)
//   -webkit-text-decoration-skip: objects;
// }

// a:hover {
//   color: $link-hover-color;
//   text-decoration: $link-hover-decoration;
// }


//
// Moved to links.scss
//
// And undo these styles for placeholder links/named anchors (without href)
// which have not been made explicitly keyboard-focusable (without tabindex).
// It would be more straightforward to just use a[href] in previous block, but that
// causes specificity issues in many other styles that are too complex to fix.
// See https://github.com/twbs/bootstrap/issues/19402
//

// a:not([href]):not([tabindex]) {
//   color: inherit;
//   text-decoration: none;
// }

// a:not([href]):not([tabindex]):hover,
// a:not([href]):not([tabindex]):focus {
//   color: inherit;
//   text-decoration: none;
// }

// a:not([href]):not([tabindex]):focus {
//   outline: 0;
// }


//
// Code
//

pre,
code,
kbd,
samp {
  font-family: monospace;
  // normalize.css: font-family: monospace, monospace; // Correct the inheritance and scaling of font size in all browsers.
  font-size: 1em; // Correct the odd `em` font sizing in all browsers. (normalize.css)
}

pre {
  // Remove browser default top margin
  margin-top: 0;
  // Reset browser default of `1em` to use `rem`s
  // margin-bottom: 1rem;
  margin-bottom: 1em;
  // Don't allow content to break outside
  overflow: auto;
  // We have @viewport set which causes scrollbars to overlap content in IE11 and Edge, so
  // we force a non-overlapping, non-auto-hiding scrollbar to counteract.
  -ms-overflow-style: scrollbar;
}


//
// Figures
//

figure {
  // Apply a consistent margin strategy (matches our type styles).
  // margin: 0 0 1rem;
  margin: 0 0 1em;
}


//
// Images and content
//

img {
  vertical-align: middle;
  border-style: none; // Remove the border on images inside links in IE 10-. (normalize.css)
}

svg:not(:root) {
  overflow: hidden; // Hide the overflow in IE
}


//
// Tables
//

table {
  border-collapse: collapse; // Prevent double borders
}

caption {
  // padding-top: $table-cell-padding; // Removed
  // padding-bottom: $table-cell-padding; // Removed
  // color: $table-caption-color; // Removed
  text-align: left;
  caption-side: bottom;
}

th {
  // Matches default `<td>` alignment by inheriting from the `<body>`, or the
  // closest parent with a set `text-align`.
  text-align: inherit;
}


//
// Forms
//

label {
  // Allow labels to use `margin` for spacing.
  display: inline-block;
  // margin-bottom: $label-margin-bottom; // Removed TODO
}

// Remove the default `border-radius` that macOS Chrome adds.
//
// Details at https://github.com/twbs/bootstrap/issues/24093
button {
  border-radius: 0;
}

// Work around a Firefox/IE bug where the transparent `button` background
// results in a loss of the default `button` focus styles.
//
// Credit: https://github.com/suitcss/base/
button:focus {
  outline: 1px dotted;
  outline: 5px auto -webkit-focus-ring-color;
}

input,
button,
select,
optgroup,
textarea {
  margin: 0; // Remove the margin in Firefox and Safari (normalize.css)
  font-family: inherit; // normalize.css
  font-size: inherit;
  // normalize.css: font-size: 100%;
  line-height: inherit;
  // normalize.css: line-height: 1.15;
}

button,
input {
  overflow: visible; // Show the overflow in Edge (normalize.css)
}

button,
select {
  text-transform: none; // Remove the inheritance of text transform in Firefox (noramlize.css)
}

// 1. Prevent a WebKit bug where (2) destroys native `audio` and `video`
//    controls in Android 4.
// 2. Correct the inability to style clickable types in iOS and Safari. (normalize.css)
button,
html [type="button"], // 1
// normalize.css: [type="button"]
[type="reset"],
[type="submit"] {
  -webkit-appearance: button; // 2 (normalize.css)
}

// Remove inner border and padding from Firefox, but don't restore the outline like Normalize.
button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0; // normalize.css
  border-style: none; // normalize.css
}

// normalize.css:
//
// /**
// * Restore the focus styles unset by the previous rule.
// */
//
// button:-moz-focusring,
// [type="button"]:-moz-focusring,
// [type="reset"]:-moz-focusring,
// [type="submit"]:-moz-focusring {
//   outline: 1px dotted ButtonText;
// }

input[type="radio"],
input[type="checkbox"] {
  box-sizing: border-box; // 1. Add the correct box sizing in IE 10- (normalize.css)
  padding: 0; // 2. Remove the padding in IE 10- (normalize.css)
}


input[type="date"],
input[type="time"],
input[type="datetime-local"],
input[type="month"] {
  // Remove the default appearance of temporal inputs to avoid a Mobile Safari
  // bug where setting a custom line-height prevents text from being vertically
  // centered within the input.
  // See https://bugs.webkit.org/show_bug.cgi?id=139848
  // and https://github.com/twbs/bootstrap/issues/11266
  -webkit-appearance: listbox;
}

textarea {
  overflow: auto; // Remove the default vertical scrollbar in IE. (normalize.css)
  // Textareas should really only resize vertically so they don't break their (horizontal) containers.
  resize: vertical;
}

fieldset {
  // Browsers set a default `min-width: min-content;` on fieldsets,
  // unlike e.g. `<div>`s, which have `min-width: 0;` by default.
  // So we reset that to ensure fieldsets behave more like a standard block element.
  // See https://github.com/twbs/bootstrap/issues/12359
  // and https://html.spec.whatwg.org/multipage/#the-fieldset-and-legend-elements
  min-width: 0;
  // Reset the default outline behavior of fieldsets so they don't affect page layout.
  padding: 0;
  margin: 0;
  border: 0;
}

// normalize.css:
//
// /**
// * Correct the padding in Firefox.
// */
//
// fieldset {
//   padding: 0.35em 0.75em 0.625em;
// }

// 1. Correct the text wrapping in Edge and IE. (normalize.css)
// 2. Correct the color inheritance from `fieldset` elements in IE. (normalize.css)
// 3. Remove the padding so developers are not caught out when they zero out
//    `fieldset` elements in all browsers. (normalize.css)
legend {
  display: block;
  // normalize.css: display: table; // 1
  // normalize.css: box-sizing: border-box; // 1
  width: 100%;
  max-width: 100%; // 1 (normalize.css)
  padding: 0; // 3 (normalize.css)
  // margin-bottom: .5rem;
  margin-bottom: .5em;
  // font-size: 1.5rem;
  font-size: 1.5em;
  line-height: inherit;
  color: inherit; // 2 (normalize.css)
  white-space: normal; // 1 (normalize.css)
}

progress {
  vertical-align: baseline; // Add the correct vertical alignment in Chrome, Firefox, and Opera. (normalize.css)
}

// Correct the cursor style of increment and decrement buttons in Chrome. (normalize.css)
[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto; // normalize.css
}

[type="search"] {
  // This overrides the extra rounded corners on search inputs in iOS so that our
  // `.form-control` class can properly style them. Note that this cannot simply
  // be added to `.form-control` as it's not specific enough. For details, see
  // https://github.com/twbs/bootstrap/issues/11586.
  outline-offset: -2px; // 2. Correct the outline style in Safari. (normalize.css)
  // -webkit-appearance: none;
  // normalize.css: -webkit-appearance: textfield; // Correct the odd appearance in Chrome and Safari
  -webkit-appearance: textfield;
}

//
// Remove the inner padding and cancel buttons in Chrome and Safari on macOS.
//

[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration { // normalize.css
  -webkit-appearance: none; // normalize.css
}

//
// 1. Correct the inability to style clickable types in iOS and Safari. (normalize.css)
// 2. Change font properties to `inherit` in Safari. (normalize.css)
//

::-webkit-file-upload-button {
  font: inherit; // 2 (normalize.css)
  -webkit-appearance: button; // 1 (normalize.css)
}

//
// Correct element displays
//

// normalize.css:
//
// /*
// * Add the correct display in Edge, IE 10+, and Firefox.
// */
//
details {
  display: block;
}

output {
  display: inline-block;
}

summary {
  display: list-item; // Add the correct display in all browsers (normalize.css)
  cursor: pointer;
}

template {
  display: none; // Add the correct display in IE (normalize.css)
}

// Always hide an element with the `hidden` HTML attribute (from PureCSS).
// Needed for proper display in IE 10-.
// normalize.css: Add the correct display in IE 10.
[hidden] {
  display: none !important; // (normalize.css)
}
